@font-face {
	font-family: 'Exo2';
	src:
			url('./../fonts/Exo 2 Medium.eot') format('eot'),
			url('./../fonts/Exo 2 Medium.ttf') format('ttf'),
			url('./../fonts/Exo 2 Medium.woff') format('woff'),
			url('./../fonts/Exo 2 Medium.woff2') format('woff2');
	font-weight: normal;
	font-style: normal;
}

